body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}


.title {
    display: flex;
    margin: 0 auto;
    padding: 5px 10%;
    height: 64px;
}
.title .right {
    padding-left: 25px;
    display: flex;
    align-items: center;
}

.title h1 {
    margin: 0;
    font-size: 24px;
}
.title p {
    margin: 0;
}
.title a {
    color: blue;
    text-decoration: none;
    margin: 0 2px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

pre.debug {
    background: #222;
    color: #ccc;
    padding: 15px;
    border-radius: 4px;
}

.gh-example {
    display: flex;
    flex-wrap: wrap;
}

.gh-100 {
    width: 100%;
}

.gh-50 {
    width: 50%;
    height: calc(100vh-64px);
    padding: 25px;
}

.gh-50 > h1 {
    text-align: center;
    margin: 0 0 15px;
    font-size: 20px;
}

.gh-50 > .box-1 {
    width: 80%;
    margin: 0 auto;
}

.gh-50 > .box-1 > pre {
    height: 80vh;
    overflow: auto;
}

.gh-50 > .box-2 {
    height: 80vh;
    overflow-y: scroll;
    width: 80%;
    margin: 0 auto;
    background: rgb(245, 242, 240);
}

.form {
    display:flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.form form {
    width: 400px;
    max-width:100%;
    margin: 25px auto;
}
.form-group,.form-group-check {
    margin-bottom: 15px;
}
.form-group > label {
    display: block;
    font-weight: bold;
    margin-bottom: 3px;
}
.form-group input[type='text'],
.form-group input[type='email'] {
    height: 36px;
    width: 100%;
    font-size: 16px;
    padding: 4px 12px;
    border-radius: 4px;
    border: 1px solid #ddd;
}
.form-group.with-nested > label {
    margin-bottom: 5px;
}

.form-group .help-text {
    margin-top: 3px;
    color: red;
    font-size: 12px;
    margin-bottom: 0px;
}

button {
    appearance: none;
    background: #005CC8;
    color: #fff;
    font-size: 18px;
    padding: 5px 12px;
    border-radius: 4px;
    border: none;
    margin-bottom: 15px;
    display: inline-block;
}

.results {
    border-top: 1px solid #ccc;
    padding-top: 10px;
}
.result-50 {
    margin-bottom: 15px;
}

.user-type-select {
    display: flex;
    justify-content: space-between;
    margin-top: 5px;
}
.user-type-option {
    width: 48%;
    text-align: center;
    border:  2px solid #ddd;
    padding:  4px 12px;
    font-size: 18px;
    border-radius: 4px;
    user-select: none;
    cursor: pointer;
    background: #fff;
}
.user-type-option.selected {
    border-color: #005CC8;
}